.item {
  width: 25%;
}

.img {
  display: block;
  width: 100%;
  height: 301px;
  object-fit: cover;

  border-radius: 50px;
}

@media (max-width: 1220px) {
  .item {
    width: 33%;
  }

  .mediumHide {
    display: none;
  }
}

@media (max-width: 900px) {
  .item {
    width: 50%;
  }

  .img {
    border-radius: 25px;
  }

  .smallHide {
    display: none;
  }
}

@media (max-width: 750px) {
  .img {
    height: 250px;
  }
}

@media (max-width: 600px) {
  .img {
    height: 175px;
  }
}

@media (max-width: 500px) {
  .img {
    border-radius: 20px;
  }
}

@media (max-width: 480px) {
  .img {
    height: 140px;
  }
}

@media (max-width: 390px) {
  .img {
    height: 110px;
  }
}
